.Header {
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 2vw;
    color: black;
    border-bottom: 2px solid black;
  }
  
  .Items {
    background-color: white;
    width: 80%;
    margin-left: 10vw;
    margin-right: 10vw;
    display: flex;
    flex-direction: row;
    color: black;
    border: 2px solid black;
  }
  button:hover {
    opacity: 0.8;
  }
  
  .Icon {
    width: 20%;
    float: left;
  }
  .col-25 {
    float: left;
    width: 25%;
    margin-top: 6px;
  }
  
  .col-75 {
    float: left;
    width: 70%;
    margin-top: 6px;
  }
  
  .row:after {
    content: "";
    display: table;
    clear: both;
    padding: 5px;
  }
  
  .Icon-img {
    padding: 1vw;
    margin-top: 2vw;
    width: 50%;
    margin-left: 1vw;
    background-repeat: no-repeat;
    background-size: 40%;
    align-items: center;
  }
  
  .Comment {
    float: right;
    width: 85%;
    text-align: left;
    overflow: hidden;
  }
  .Feedback {
    margin-top: 5%;
    margin-bottom: 10%;
  }
  .form {
    width: 30%;
    margin-left: 20vw;
    margin-right: 20vw;
    margin-top: 2vw;
    border-radius: 1px;
    display: flex;
    flex-direction: column;
    border: 1px solid #000000;
    background-color: #ffffff;
    padding: 20px;
    margin-right: 10%;
    margin-bottom: 10%;
  }
  .add_btn {
    background-color: #1e65c2;
    color: white;
    padding: 6px 10px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    float: right;
  }
  .sub_btn {
    background-color: #1e65c2;
    color: white;
    padding: 12px 20px;
    border: none;
    border-radius: 2px;
    cursor: pointer;
    float: right;
    margin-right: 2vw;
    margin-top: 1vw;
  }
  .errmsg{
    color:red;
  }
  .showFb_btn {
    background-color: #1e65c2;
    color: white;
    padding: 6px 10px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    float: right;
  }
  .edit_icon{
    padding: 1vw;
    background-size: 10%;
    width: 30%;
    float:right;
  }
  .edit-btn{
    width: 100%;
    height: 5%;
  }
  .img{
    padding: 1vw;
    background-size: 5%;
    width: 10%;
    float:right;
  
  }